import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import { Link } from "react-router-dom";
import validator from "validator";
import Loader from "../components/Loader";
import { updateLandlord, getlandlords } from "../actions/landlordActions";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";

const LandlordEditScreen = ({ history, match }) => {
  const Id = match.params.id;
  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [idNumber, setIdNumber] = useState();
  const [phoneNumber, setPhoneNumber] = useState();

  const validatePhoneNumber = (phoneNumber) => {
    const isValidPhoneNumber = validator.isMobilePhone(phoneNumber);
    return isValidPhoneNumber;
  };
  const dispatch = useDispatch();

  const updateLandlords = useSelector((state) => state.updateLandlords);
  const { loading, success, error } = updateLandlords;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  const listLandlord = useSelector((state) => state.listLandlord);
  const { landlords } = listLandlord;

  const getLandlord = useSelector((state) => state.getLandlord);
  const {
    loading: loadingGetLandlord,
    landlord,
    error: errorgetLandlord,
  } = getLandlord;
  useEffect(() => {
    if (!userInfo) {
      history.push("/");
    }
    if (landlord.name || landlord._id !== Id) {
      dispatch(getlandlords(Id));
    } else {
      setFullName(landlord.fullName);
      setEmail(landlord.email);
      setIdNumber(landlord.idNumber);
      setPhoneNumber(landlord.phoneNumber);
    }
  }, [dispatch, history, Id, landlord, userInfo]);
  const onSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateLandlord({ _id: Id, fullName, email, idNumber, phoneNumber })
    );
  };
  return (
    <>
      <Sidebar />

      <div className="container">
        <Navbar />
        <div className="card o-hidden border-0 shadow-lg my-5">
          <div className="card-body p-0">
            <div className="row">
              <div className="col-lg-12 col-md-12">
                <div className="p-5">
                  <div className="text-center">
                    <Link to="/listlandlords" className="btn btn-outline-info">
                      Go Back
                    </Link>
                    {error && toast({ error })}
                    <br />
                    {success && toast("Landlord Updated successfully")}
                    <h1 className="h4 text-gray-900 mb-4">Edit Landlord</h1>
                  </div>
                  <form className="user" onSubmit={onSubmitHandler}>
                    <div class="form-group">
                      <input
                        type="text"
                        value={fullName}
                        onChange={(e) => setFullName(e.target.value)}
                        class="form-control form-control-user"
                        id="exampleInputName"
                        required
                        minLength="6"
                        aria-describedby="name"
                        placeholder="Enter Name..."
                      />
                    </div>
                    <div class="form-group">
                      <input
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        class="form-control form-control-user"
                        id="exampleInputEmail"
                        required
                        aria-describedby="emailHelp"
                        placeholder="Enter Email Address..."
                      />
                    </div>
                    <div class="form-group">
                      <input
                        type="number"
                        value={idNumber}
                        onChange={(e) => setIdNumber(e.target.value)}
                        class="form-control form-control-user"
                        id="number"
                        required
                        minLength="8"
                        aria-describedby="emailHelp"
                        placeholder="Enter Id Number..."
                      />
                    </div>
                    <div class="form-group">
                      <input
                        type="number"
                        value={phoneNumber}
                        onChange={(e) => setPhoneNumber(e.target.value)}
                        class="form-control form-control-user"
                        id="password"
                        required
                        minLength="10"
                        aria-describedby="emailHelp"
                        placeholder="Enter Phone Number..."
                      />
                    </div>

                    <div class="form-group">
                      {loading ? (
                        <Loader />
                      ) : (
                        <button
                          type="submit"
                          class="btn btn-primary btn-user ml-auto"
                        >
                          Update Landlord
                        </button>
                      )}
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default LandlordEditScreen;
