import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Loader from "../components/Loader";
import { useDispatch, useSelector } from "react-redux";
import Message from "../components/Message";
import { login } from "../actions/userActions";
const LoginScreen = ({ history, location }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { error, loading, userInfo } = userLogin;

  useEffect(() => {
    if (userInfo) {
      history.push("/dashboard");
    }
  }, [userInfo, history]);
  const onSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(login(email, password));
  };
  return (
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-xl-10 col-lg-12 col-md-9">
          <div class="card o-hidden border-0 shadow-lg my-5">
            <div class="card-body p-0">
              <div class="row">
                <div class="col-lg-6 d-none d-lg-block">
                  <img
                    className="img-fluid"
                    src="https://images.pexels.com/photos/4913427/pexels-photo-4913427.jpeg"
                    alt="House"
                  />
                </div>
                <div class="col-lg-6">
                  <div class="p-5">
                    <div class="text-center">
                      <h1 class="h4 text-gray-900 mb-4">
                        Welcome to Manyatta Rental System
                      </h1>
                      {error && <Message> {error} </Message>}
                    </div>
                    <form class="user" onSubmit={onSubmitHandler}>
                      <div class="form-group">
                        <input
                          type="email"
                          value={email}
                          onChange={(e) => setEmail(e.target.value)}
                          class="form-control form-control-user"
                          id="exampleInputEmail"
                          required
                          aria-describedby="emailHelp"
                          placeholder="Enter Email Address..."
                        />
                      </div>
                      <div class="form-group">
                        <input
                          type="password"
                          value={password}
                          onChange={(e) => setPassword(e.target.value)}
                          class="form-control form-control-user"
                          id="password"
                          required
                          aria-describedby="emailHelp"
                          placeholder="Enter Password..."
                        />
                      </div>

                      <div class="form-group">
                        {loading ? (
                          <Loader />
                        ) : (
                          <button
                            type="submit"
                            class="btn btn-primary btn-user btn-block"
                          >
                            Login
                          </button>
                        )}
                        <hr />
                        <Link
                          to="index.html"
                          class="btn btn-google btn-user btn-block"
                        >
                          <i class="fab fa-google fa-fw"></i> Login with Google
                        </Link>
                        <Link
                          to="index.html"
                          class="btn btn-facebook btn-user btn-block"
                        >
                          <i class="fab fa-facebook-f fa-fw"></i> Login with
                          Facebook
                        </Link>
                      </div>
                    </form>
                    <hr />
                    <div class="text-center">
                      <Link class="small" to="forgot-password.html">
                        Forgot Password?
                      </Link>
                    </div>
                    <div class="text-center">
                      <Link class="small" to="/register">
                        Create an Account!
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginScreen;
