import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import Loader from "../components/Loader";
import Message from "../components/Message";
import { toast } from "react-toastify";
import { useDispatch, useSelector } from "react-redux";
import Select from "react-select";
import { tenantList } from "../actions/tenantActions";
import { propertyList } from "../actions/propertyActions";
import { roomUpdate, roomGet } from "../actions/roomActions";
import { Link } from "react-router-dom";
import { ROOM_UPDATE_RESET } from "../constants/roomContants";
import { Button } from "react-bootstrap";

const EditRoomScreen = ({ history, match }) => {
  const Id = match.params.id;
  var roomType = [
    {
      value: "singleroom",
      label: "Single Room",
    },
    {
      value: "bedsitter",
      label: "Bedsitter",
    },
    {
      value: "onebedroom",
      label: "One Bedroom",
    },
    {
      value: "twobedroom",
      label: "Two Bedroom",
    },
    {
      value: "threebedroom",
      label: "Three Bedroom",
    },
  ];
  const date = new Date();

  const [title, setTitle] = useState("");
  const [rent, setRent] = useState("");
  const [isOccupied, setIsOccupied] = useState(false);
  const [deposit, setDeposit] = useState("");
  const [startDate, setStartDate] = useState(date);
  const [parentApartment, setParentApartment] = useState("");
  const [tenant, setTenant] = useState("607fdccd3f9cfc52e84d06d0");
  const [type, setRoomType] = useState(roomType.value);

  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const listProperty = useSelector((state) => state.listProperty);
  const { propertys } = listProperty;

  const updateRoom = useSelector((state) => state.updateRoom);
  const { success, error, loading } = updateRoom;
  const getRoom = useSelector((state) => state.getRoom);
  const { room } = getRoom;

  const listTenants = useSelector((state) => state.listTenants);
  const { tenants } = listTenants;

  useEffect(() => {
    if (!userInfo) {
      history.push("/");
    }
    if (room.name || room._id !== Id) {
      dispatch(roomGet(Id));
      dispatch(tenantList());
      dispatch(propertyList());
    } else {
      setTitle(room.title);
      setParentApartment(room.parentApartment);
      setDeposit(room.deposit);
      setRent(room.rent);
      setRoomType(room.type);
      setStartDate(room.startDate);
      setIsOccupied(room.isOccupied);
      setTenant(room.tenant);
    }
    if (success) {
      history.push("/listrooms");
      dispatch({ type: ROOM_UPDATE_RESET });
    }
  }, [dispatch, room, history, Id, success, userInfo]);
  const onSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(
      roomUpdate({
        _id: Id,
        title,
        rent,
        isOccupied,
        deposit,
        startDate,
        parentApartment,
        tenant,
        type,
      })
    );
  };
  return (
    <>
      <Sidebar />

      <div className="container">
        <Navbar />
        <div className="card o-hidden border-0 shadow-lg my-5">
          <div className="card-body p-0" style={{ height: "80vh " }}>
            <div className="row">
              <div className="col-lg-12 col-md-12">
                <div className="p-5">
                  <div className="text-center">
                    {error && <Message> {error} </Message>}
                    {success && toast("Room updated successfully")}
                    <Link to="/listrooms">
                      {" "}
                      <Button className="btn btn-outline-info">
                        VIEW ALL ROOMS
                      </Button>{" "}
                    </Link>
                    <br />
                    <h1 className="h4 text-gray-900 mb-4">Update Room</h1>
                  </div>
                  <form className="user" onSubmit={onSubmitHandler}>
                    <div className="row">
                      <div className="col">
                        <div class="form-group">
                          <input
                            type="text"
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            class="form-control form-control-user"
                            id="exampleInputName"
                            required
                            aria-describedby="name"
                            placeholder="Enter Rooms Name..."
                          />
                        </div>
                      </div>
                      <div className="col">
                        <div class="form-group">
                          <input
                            type="number"
                            value={rent}
                            onChange={(e) => setRent(e.target.value)}
                            class="form-control form-control-user"
                            id="exampleInputName"
                            required
                            aria-describedby="name"
                            placeholder="Enter Rent payable..."
                          />
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <input
                            type="number"
                            value={deposit}
                            onChange={(e) => setDeposit(e.target.value)}
                            class="form-control form-control-user"
                            id="exampleInputEmail"
                            required
                            aria-describedby="emailHelp"
                            placeholder="Enter deposit paid..."
                          />
                        </div>
                      </div>
                      <div className="col">
                        <div class="form-group">
                          <label className="form-check-label">
                            Is Occupied{" "}
                          </label>
                          <input
                            className="form-check"
                            type="checkbox"
                            checked={isOccupied}
                            onChange={(e) => setIsOccupied(e.target.checked)}
                          />
                        </div>
                      </div>
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <input
                            type="date"
                            value={startDate}
                            onChange={(e) => setStartDate(e.target.value)}
                            class="form-control form-control-user"
                            id="date"
                            aria-describedby="emailHelp"
                            placeholder="Enter Start Date..."
                          />
                        </div>
                      </div>
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <label htmlFor="Occupation">Select Room type</label>
                          <Select
                            options={roomType}
                            onChange={(e) => setRoomType(e.value)}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <select
                            className=" form-control"
                            value={parentApartment}
                            onChange={(e) => {
                              const selectedLanlord = e.target.value;
                              setParentApartment(selectedLanlord);
                            }}
                          >
                            <option>Select Property Name</option>
                            {propertys.map((property) => (
                              <option value={property._id}>
                                {property.givenName}
                              </option>
                            ))}
                          </select>
                        </div>
                      </div>
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <select
                            className=" form-control"
                            value={tenant}
                            onChange={(e) => {
                              const selectedTenant = e.target.value;
                              setTenant(selectedTenant);
                            }}
                          >
                            <option value={"607fdccd3f9cfc52e84d06d1"}>
                              Select Tenant
                            </option>
                            {tenants.map((tenant) => (
                              <option value={tenant._id}>
                                {tenant.firstName + tenant.lastName}
                              </option>
                            ))}
                          </select>
                        </div>
                      </div>
                    </div>

                    <div class="form-group">
                      {loading ? (
                        <Loader />
                      ) : (
                        <button
                          type="submit"
                          class="btn btn-primary btn-user ml-auto"
                        >
                          Update Room
                        </button>
                      )}
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default EditRoomScreen;
