import React from "react";
import Dashboard from "./components/Dashboard";
import LoginScreen from "../src/screens/LoginScreen";
import RegisterScreen from "../src/screens/RegisterScreen";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import AddLandlord from "./screens/AddLandlord";
import ListLandlordsScreen from "./screens/ListLandlordsScreen";
import LandlordEditScreen from "./screens/LandlordEditScreen";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import AddTenant from "./screens/AddTenant";
import ListTenantsScreen from "./screens/ListTenantsScreen";
import EditTenantsScreen from "./screens/EditTenantsScreen";
import AddPropertyScreen from "./screens/AddPropertyScreen";
import ListPropertyScreen from "./screens/ListPropertyScreen";
import EditPropertyScreen from "./screens/EditPropertyScreen";
import AddRoomScreen from "./screens/AddRoomScreen";
import ListRoomsScreen from "./screens/ListRoomsScreen";
import EditRoomScreen from "./screens/EditRoomScreen";
import PageNotFound from "./screens/PageNotFound";

const App = () => {
  return (
    <div id="wrapper">
      <ToastContainer />
      <Router>
        <Switch>
          <Route path="/landlords" exact component={AddLandlord} />
          <Route path="/property" exact component={AddPropertyScreen} />
          <Route path="/rooms" exact component={AddRoomScreen} />
          <Route path="/listrooms" exact component={ListRoomsScreen} />
          <Route path="/listrooms/:id" exact component={EditRoomScreen} />
          <Route path="/listproperty" exact component={ListPropertyScreen} />
          <Route
            path="/listproperty/:id"
            exact
            component={EditPropertyScreen}
          />
          <Route path="/dashboard" exact component={Dashboard} />
          <Route path="/tenants" exact component={AddTenant} />
          <Route path="/listtenants" exact component={ListTenantsScreen} />
          <Route path="/listtenants/:id" exact component={EditTenantsScreen} />
          <Route path="/listlandlords" exact component={ListLandlordsScreen} />
          <Route path="/register" component={RegisterScreen} />
          <Route
            path="/listlandlords/:id"
            exact
            component={LandlordEditScreen}
          />
          <Route path="/" component={LoginScreen} exact />
          <Route component={PageNotFound} />
        </Switch>
      </Router>
    </div>
  );
};

export default App;
