import {
  TENANT_DELETE_FAIL,
  TENANT_DELETE_REQUEST,
  TENANT_DELETE_SUCCESS,
  TENANT_DETAILS_FAIL,
  TENANT_DETAILS_REQUEST,
  TENANT_DETAILS_SUCCESS,
  TENANT_LIST_FAIL,
  TENANT_LIST_REQUEST,
  TENANT_LIST_SUCCESS,
  TENANT_REGISTER_FAIL,
  TENANT_REGISTER_REQUEST,
  TENANT_REGISTER_RESET,
  TENANT_REGISTER_SUCCESS,
  TENANT_UPDATE_FAIL,
  TENANT_UPDATE_REQUEST,
  TENANT_UPDATE_SUCCESS,
} from "../constants/tenantConstants";
import axios from "axios";
import { LANDLORD_UPDATE_REQUEST } from "../constants/landlordConstants";
export const addTenant =
  (
    firstName,
    lastName,
    email,
    phoneNumber,
    idNumber,
    emergencyPhoneNumber,
    emergencyName,
    occupation,
    roomAssigned
  ) =>
  async (dispatch, getState) => {
    try {
      dispatch({
        type: TENANT_REGISTER_REQUEST,
      });
      const {
        userLogin: { userInfo },
      } = getState();
      const config = {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      const { data } = await axios.post(
        "/api/tenants",
        {
          firstName,
          lastName,
          email,
          phoneNumber,
          idNumber,
          emergencyPhoneNumber,
          emergencyName,
          occupation,
          roomAssigned,
        },
        config
      );
      dispatch({
        type: TENANT_REGISTER_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: TENANT_REGISTER_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      });
    }
  };

export const tenantList = () => async (dispatch, getState) => {
  try {
    dispatch({
      type: TENANT_LIST_REQUEST,
    });
    const {
      userLogin: { userInfo },
    } = getState();
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await axios.get("/api/tenants", config);
    dispatch({
      type: TENANT_LIST_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: TENANT_LIST_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    });
  }
};

