import React, { useState, useEffect } from "react";
import Footer from "./Footer";
import Navbar from "./Navbar";
import { useSelector, useDispatch } from "react-redux";
import { listLandlords } from "../actions/landlordActions";
import { tenantList } from "../actions/tenantActions";
import { propertyList } from "../actions/propertyActions";
import { roomList } from "../actions/roomActions";
const MainArea = () => {
  const dispatch = useDispatch();
  const listLandlord = useSelector((state) => state.listLandlord);
  const { landlords } = listLandlord;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const listTenants = useSelector((state) => state.listTenants);
  const { tenants } = listTenants;

  const listProperty = useSelector((state) => state.listProperty);
  const { propertys } = listProperty;

  const listRooms = useSelector((state) => state.listRooms);
  const { rooms } = listRooms;

  useEffect(() => {
    if (userInfo) {
      dispatch(listLandlords());
      dispatch(tenantList());
      dispatch(propertyList());
      dispatch(roomList());
    }
  }, [userInfo, dispatch]);

  return (
    <div className="container-fluid">
      <Navbar />
      <div>
        <div className="d-sm-flex align-items-center justify-content-between mb-4">
          <h1 className="h3 mb-0 text-gray-800">Dashboard</h1>
          {/* <a
          to="#"
          className="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm"
        >
          <i className="fas fa-download fa-sm text-white-50"></i> 
        </a> */}
        </div>

        <div className="row">
          <div className="col-xl-3 col-md-6 mb-4">
            <div className="card border-left-primary shadow h-100 py-2">
              <div className="card-body">
                <div className="row no-gutters align-items-center">
                  <div className="col mr-2">
                    <div className="text-xs font-weight-bold text-primary text-uppercase mb-1">
                      Landlords
                    </div>
                    <div className="h5 mb-0 font-weight-bold text-gray-800">
                      {landlords.length}
                    </div>
                  </div>
                  <div className="col-auto">
                    <i className="fas fa-user fa-2x "></i>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-xl-3 col-md-6 mb-4">
            <div className="card border-left-success shadow h-100 py-2">
              <div className="card-body">
                <div className="row no-gutters align-items-center">
                  <div className="col mr-2">
                    <div className="text-xs font-weight-bold text-success text-uppercase mb-1">
                      Tenants
                    </div>
                    <div className="h5 mb-0 font-weight-bold text-gray-800">
                      {tenants.length}
                    </div>
                  </div>
                  <div className="col-auto">
                    <i className="fas fa-user-circle fa-2x "></i>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-xl-3 col-md-6 mb-4">
            <div className="card border-left-info shadow h-100 py-2">
              <div className="card-body">
                <div className="row no-gutters align-items-center">
                  <div className="col mr-2">
                    <div className="text-xs font-weight-bold text-info text-uppercase mb-1">
                      Properties
                    </div>
                    <div className="h5 mb-0 font-weight-bold text-gray-800">
                      {propertys.length}
                    </div>
                    <div className="text-xs font-weight-bold text-info text-uppercase mb-1">
                      Rooms
                    </div>
                    <div className="h5 mb-0 font-weight-bold text-gray-800">
                      {rooms.length}
                    </div>
                  </div>

                  <div className="col-auto">
                    <i className="fas fa-building fa-2x "></i>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-xl-3 col-md-6 mb-4">
            <div className="card border-left-warning shadow h-100 py-2">
              <div className="card-body">
                <div className="row no-gutters align-items-center">
                  <div className="col mr-2">
                    <div className="text-xs font-weight-bold text-warning text-uppercase mb-1">
                      Invoices
                    </div>
                    <div className="h5 mb-0 font-weight-bold text-gray-800">
                      18
                    </div>
                  </div>
                  <div className="col-auto">
                    <i className="fas fa-sticky-note fa-2x"></i>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default MainArea;
