import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import Select from "react-select";
import Loader from "../components/Loader";
import { listLandlords } from "../actions/landlordActions";
import { addProperty } from "../actions/propertyActions";
import { tenantList } from "../actions/tenantActions";
import { useDispatch, useSelector } from "react-redux";
import { LANDLORD_REGISTER_RESET } from "../constants/landlordConstants";
import { toast } from "react-toastify";
import Message from "../components/Message";
import { PROPERTY_REGISTER_RESET } from "../constants/propertyConstants";
const AddPropertyScreen = ({ history }) => {
  const typeList = [
    {
      label: "apartment",
      value: "apartment",
    },

    {
      label: "bungalow",
      value: "bungalow",
    },
  ];
  const [name, setName] = useState("");
  const [landlord, setLandlord] = useState("6083dbbe0793cb36a4bad3e3");
  const [deposit, setDeposit] = useState();
  const [type, setType] = useState(typeList.value);
  const [rent, setRent] = useState();
  const [city, setCity] = useState("");
  const [county, setCounty] = useState("");
  const [isOccupied, setIsOccupied] = useState(false);
  const [tenant, setTenant] = useState("6083dbbe0793cb36a4bad3e3");
  const [numRooms, setNumRooms] = useState();

  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  const registerProperty = useSelector((state) => state.registerProperty);
  const {
    loading: loadingProperty,
    error: errorProperty,
    success,
  } = registerProperty;

  const listLandlord = useSelector((state) => state.listLandlord);
  const { error, loading, landlords } = listLandlord;
  const listTenants = useSelector((state) => state.listTenants);
  const { tenants } = listTenants;

  useEffect(() => {
    if (!userInfo) {
      history.push("/");
    } else {
      dispatch(listLandlords());
      dispatch(tenantList());
      if (success) {
        history.push("/listproperty");
      }
    }
  }, [history, userInfo, dispatch, success]);
  const onSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(
      addProperty(
        name,
        landlord,
        deposit,
        type,
        rent,
        city,
        county,
        isOccupied,
        tenant,
        numRooms
      )
    );
  };
  return (
    <>
      {userInfo && (
        <>
          <Sidebar />

          <div className="container">
            <Navbar />
            <div className="card o-hidden border-0 shadow-lg my-5">
              <div className="card-body p-0">
                <div className="row">
                  <div className="col-lg-12 col-md-12">
                    <div className="p-5">
                      <div className="text-center">
                        {errorProperty && <Message>{errorProperty} </Message>}
                        {error && toast({ error })}

                        {success && toast("Property registered successfully")}
                        <h1 className="h4 text-gray-900 mb-4">Add Property</h1>
                      </div>
                      <form className="user" onSubmit={onSubmitHandler}>
                        <div className="row">
                          <div className="col-md-4">
                            <div class="form-group">
                              <input
                                type="text"
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                                class="form-control form-control-user"
                                id="name"
                                required
                                aria-describedby="emailHelp"
                                placeholder=" Enter Name of the apartment/Bungalow..."
                              />
                            </div>
                          </div>
                          <div className="col-md-4">
                            <div class="form-group">
                              <label htmlFor="Occupation">
                                Select Property Type
                              </label>
                              <Select
                                options={typeList}
                                onChange={(e) => setType(e.value)}
                              />
                            </div>
                          </div>
                          <div className="col-md-4">
                            <div class="form-group">
                              <input
                                type="text"
                                value={county}
                                onChange={(e) => setCounty(e.target.value)}
                                class="form-control form-control-user"
                                id=""
                                required
                                aria-describedby="emailHelp"
                                placeholder="Enter County..."
                              />
                            </div>
                          </div>

                          {/* end row 1 */}
                        </div>
                        <div className="row">
                          {type === "bungalow" && (
                            <div className="col-md-4">
                              <div class="form-group">
                                <input
                                  type="number"
                                  value={deposit}
                                  onChange={(e) => setDeposit(e.target.value)}
                                  class="form-control form-control-user"
                                  id="number"
                                  required
                                  aria-describedby="emailHelp"
                                  placeholder="Enter Deposit paid(Default=0)..."
                                />
                              </div>
                            </div>
                          )}
                          <div className="col-md-4">
                            <div class="form-group">
                              <input
                                type="text"
                                value={city}
                                onChange={(e) => setCity(e.target.value)}
                                class="form-control form-control-user"
                                id="city"
                                required
                                aria-describedby="emailHelp"
                                placeholder="Enter City..."
                              />
                            </div>
                          </div>
                          <div className="col-md-4">
                            <select
                              className=" form-control"
                              value={landlord}
                              onChange={(e) => {
                                const selectedLanlord = e.target.value;
                                setLandlord(selectedLanlord);
                              }}
                            >
                              <option>Select Landlord</option>
                              {landlords.map((landlord) => (
                                <option value={landlord._id}>
                                  {landlord.fullName}
                                </option>
                              ))}
                            </select>
                          </div>

                          {type === "bungalow" && (
                            <div className="col-md-4">
                              <div class="form-group">
                                <input
                                  type="number"
                                  value={rent}
                                  onChange={(e) => setRent(e.target.value)}
                                  class="form-control form-control-user"
                                  id="number"
                                  required
                                  aria-describedby="emailHelp"
                                  placeholder="Enter rent payable(if bungalow)..."
                                />
                              </div>
                            </div>
                          )}

                          {type === "apartment" && (
                            <div className="col-md-4">
                              <div class="form-group">
                                <input
                                  type="number"
                                  value={numRooms}
                                  onChange={(e) => setNumRooms(e.target.value)}
                                  class="form-control form-control-user"
                                  id="number"
                                  required
                                  aria-describedby="emailHelp"
                                  placeholder="Enter Number of room (if apartment)..."
                                />
                              </div>{" "}
                            </div>
                          )}

                          {type === "bungalow" && (
                            <div className="col-md-4">
                              <label className="form-check-label">
                                Is Occupied{" "}
                              </label>
                              <input
                                className="form-check"
                                type="checkbox"
                                checked={isOccupied}
                                onChange={(e) =>
                                  setIsOccupied(e.target.checked)
                                }
                              />
                            </div>
                          )}
                          {type === "bungalow" && (
                            <div className="col-md-4">
                              <div class="form-group">
                                <select
                                  className="form-control"
                                  onChange={(e) => {
                                    const selectTenant = e.target.value;
                                    setTenant(selectTenant);
                                  }}
                                >
                                  <option>Select tenant</option>
                                  {tenants.map((tenant) => (
                                    <option value={tenant._id}>
                                      {tenant.firstName + tenant.lastName}{" "}
                                    </option>
                                  ))}
                                </select>
                              </div>
                            </div>
                          )}
                        </div>

                        <div class="form-group">
                          {loadingProperty ? (
                            <Loader />
                          ) : (
                            <button
                              type="submit"
                              class="btn btn-primary btn-user ml-auto"
                            >
                              Add Property
                            </button>
                          )}
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default AddPropertyScreen;
