import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import Loader from "../components/Loader";
import { LinkContainer } from "react-router-bootstrap";
import { roomList, roomDelete } from "../actions/roomActions";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import { Badge, Button } from "react-bootstrap";
import { ROOM_REGISTER_RESET } from "../constants/roomContants";

const ListRoomsScreen = ({ history }) => {
  const dispatch = useDispatch();
  const listRooms = useSelector((state) => state.listRooms);
  const { error, loading, rooms } = listRooms;

  const deleteRoom = useSelector((state) => state.deleteRoom);
  const { success, loading: loadingDelete } = deleteRoom;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    if (userInfo) {
      dispatch(roomList());
      dispatch({ type: ROOM_REGISTER_RESET });
    } else {
      history.push("/");
    }
  }, [success, history, dispatch, userInfo]);

  const deleteHandler = (id) => {
    if (window.confirm("Are you sure you want to delete the room?")) {
      //dispatch
      dispatch(roomDelete(id));
    }
  };
  return (
    <>
      {userInfo && (
        <>
          <Sidebar />
          <div class="container-fluid overflow-hidden">
            <Navbar />
            <h1 class="h3 mb-2 text-gray-800">ROOMS</h1>
            {success && toast("Room deleted successfully")}

            <p class="mb-4">
              <Link to="/rooms">
                <Button className="btn btn-success">
                  {" "}
                  <i className="fa fa-plus"></i>Add Room{" "}
                </Button>
              </Link>
            </p>

            <div class="card shadow mb-4">
              <div class="card-header py-3">
                <h6 class="m-0 font-weight-bold text-primary">
                  VIEW ALL Rooms
                </h6>
              </div>
              <div class="card-body">
                <div class="table-responsive ">
                  <table
                    class="table table-bordered"
                    id="dataTable"
                    width="100%"
                    cellspacing="0"
                  >
                    <thead>
                      <tr>
                        <th>TITLE</th>
                        <th>PARENT APARTMENT</th>
                        <th>DEPOSIT</th>
                        <th>TYPE</th>
                        <th>RENT</th>
                        <th>IS OCCUPIED</th>
                        <th>LEASE START DATE</th>
                        <th>TENANT</th>
                        <th>ACTIONS</th>
                      </tr>
                    </thead>
                    <tfoot>
                      <tr>
                        <th>TITLE</th>
                        <th>PARENT APARTMENT</th>
                        <th>DEPOSIT</th>
                        <th>TYPE</th>
                        <th>RENT</th>
                        <th>IS OCCUPIED</th>
                        <th>LEASE START DATE</th>
                        <th>TENANT</th>
                        <th>ACTIONS</th>
                      </tr>
                    </tfoot>
                    {loading ? (
                      <Loader />
                    ) : (
                      <tbody>
                        {rooms.map((room) => (
                          <tr key={room._id}>
                            <td>{room.title} </td>

                            <td>
                              {" "}
                              {room.parentApartment &&
                                room.parentApartment.givenName}
                            </td>
                            <td> {room.deposit} </td>
                            <td> {room.type} </td>
                            <td> {room.rent} </td>
                            <td>
                              {" "}
                              {room.isOccupied === false ? (
                                <Badge className="badge badge-danger">
                                  False{" "}
                                </Badge>
                              ) : (
                                <Badge className="badge badge-success">
                                  True{" "}
                                </Badge>
                              )}{" "}
                            </td>
                            <td> {room.startDate.substring(0, 10)} </td>
                            <td>
                              {" "}
                              {room.tenant === null ? (
                                <Badge className="badge badge-danger">
                                  No tenant
                                </Badge>
                              ) : (
                                room.tenant &&
                                room.tenant.firstName +
                                  " " +
                                  room.tenant.lastName
                              )}{" "}
                            </td>
                            <td>
                              {
                                <td>
                                  <LinkContainer to={`/listrooms/${room._id}`}>
                                    <Button className="btn btn-primary btn-sm mb-2">
                                      <i className="fas fa-edit"></i>
                                    </Button>
                                  </LinkContainer>
                                  <Button
                                    onClick={() => deleteHandler(room._id)}
                                    className="btn btn-danger btn-sm"
                                  >
                                    <i className="fas fa-trash"></i>
                                  </Button>
                                </td>
                              }
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    )}
                  </table>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ListRoomsScreen;
