import {
  LANDLORD_DETAILS_FAIL,
  LANDLORD_DETAILS_REQUEST,
  LANDLORD_DETAILS_SUCCESS,
  LANDLORD_REGISTER_FAIL,
  LANDLORD_REGISTER_REQUEST,
  LANDLORD_REGISTER_RESET,
  LANDLORD_REGISTER_SUCCESS,
  LANDLORD_UPDATE_FAIL,
  LANDLORD_UPDATE_SUCCESS,
  LANDLORD_UPDATE_RESET,
  LANDLORD_UPDATE_REQUEST,
  LANDLORD_UPDATEDETAILS_FAIL,
  LANDLORD_UPDATEDETAILS_REQUEST,
  LANDLORD_UPDATEDETAILS_SUCCESS,
  LANDLORD_UPDATEDETAILS_RESET,
  LANDLORD_DELETE_RESET,
  LANDLORD_DELETE_REQUEST,
  LANDLORD_DELETE_SUCCESS,
  LANDLORD_DELETE_FAIL,
} from "../constants/landlordConstants.js";

export const registerLandlordReducer = (state = {}, action) => {
  switch (action.type) {
    case LANDLORD_REGISTER_REQUEST:
      return {
        loading: true,
      };
    case LANDLORD_REGISTER_SUCCESS:
      return { loading: false, success: true, landlord: action.payload };
    case LANDLORD_REGISTER_FAIL:
      return { loading: false, error: action.payload };
    case LANDLORD_REGISTER_RESET:
      return {};
    default:
      return state;
  }
};

export const listLandlordReducer = (state = { landlords: [] }, action) => {
  switch (action.type) {
    case LANDLORD_DETAILS_REQUEST:
      return {
        loading: true,
        landlords: [],
      };
    case LANDLORD_DETAILS_SUCCESS:
      return { loading: false, landlords: action.payload };
    case LANDLORD_DETAILS_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
export const deleteLandlordReducer = (state = {}, action) => {
  switch (action.type) {
    case LANDLORD_DELETE_REQUEST:
      return {
        loading: true,
      };
    case LANDLORD_DELETE_SUCCESS:
      return { loading: false, success: true };
    case LANDLORD_DELETE_FAIL:
      return { loading: false, error: action.payload };
    case LANDLORD_DELETE_RESET:
      return {};
    default:
      return state;
  }
};

export const updateLandlordReducer = (state = { landlord: {} }, action) => {
  switch (action.type) {
    case LANDLORD_UPDATE_REQUEST:
      return {
        loading: true,
      };
    case LANDLORD_UPDATE_SUCCESS:
      return { loading: false, success: true, payload: action.payload };
    case LANDLORD_UPDATE_FAIL:
      return { loading: false, error: action.payload };
    case LANDLORD_UPDATE_RESET:
      return {};
    default:
      return state;
  }
};

export const getLandlordReducer = (state = { landlord: {} }, action) => {
  switch (action.type) {
    case LANDLORD_UPDATEDETAILS_REQUEST:
      return {
        loading: true,
        ...state,
      };
    case LANDLORD_UPDATEDETAILS_SUCCESS:
      return { loading: false, landlord: action.payload };
    case LANDLORD_UPDATEDETAILS_FAIL:
      return { loading: false, error: action.payload };
    case LANDLORD_UPDATEDETAILS_RESET:
      return {};
    default:
      return state;
  }
};
