import React from "react";

const Loader = () => {
  return (
    <div>
      <div
        class="spinner-grow text-primary"
        role="status"
        style={{
          margin: "auto",
          display: "block",
        }}
      >
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  );
};

export default Loader;
