import React, { useEffect } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import Loader from "../components/Loader";
import { LinkContainer } from "react-router-bootstrap";
import { propertyList, propertyDelete } from "../actions/propertyActions";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import { Badge, Button } from "react-bootstrap";
import { LANDLORD_DELETE_RESET } from "../constants/landlordConstants";
import { PROPERTY_REGISTER_RESET } from "../constants/propertyConstants";

const ListPropertyScreen = ({ history }) => {
  const dispatch = useDispatch();
  const listProperty = useSelector((state) => state.listProperty);
  const { error, loading, propertys } = listProperty;

  const deleteProperty = useSelector((state) => state.deleteProperty);
  const { success, loading: loadingDelete } = deleteProperty;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    if (userInfo) {
      dispatch(propertyList());
      dispatch({ type: PROPERTY_REGISTER_RESET });
    } else {
      history.push("/");
    }
  }, [history, dispatch, userInfo, success]);

  const deleteHandler = (id) => {
    if (window.confirm("Are you sure you want to delete the property?")) {
      //dispatch
      dispatch(propertyDelete(id));
      dispatch({ type: "PROPERTY_DELETE_RESET" });
    }
  };
  return (
    <>
      {userInfo && (
        <>
          <Sidebar />
          <div class="container-fluid overflow-hidden">
            <Navbar />
            <h1 class="h3 mb-2 text-gray-800">PROPERTY</h1>
            {success && toast("Property deleted successfully")}

            <p class="mb-4">
              <Link to="/property">
                <Button className="btn btn-success">
                  {" "}
                  <i className="fa fa-plus"></i>Add Property{" "}
                </Button>
              </Link>
            </p>

            <div class="card shadow mb-4">
              <div class="card-header py-3">
                <h6 class="m-0 font-weight-bold text-primary">
                  VIEW ALL PROPERTY
                </h6>
              </div>
              <div class="card-body">
                <div class="table-responsive ">
                  <table
                    class="table table-bordered"
                    id="dataTable"
                    width="100%"
                    cellspacing="0"
                  >
                    <thead>
                      <tr>
                        <th>NAME</th>
                        <th>LANDLORD</th>
                        <th>DEPOSIT(*BUNGALOW)</th>
                        <th>TYPE</th>
                        <th>RENT</th>
                        <th>IS OCCUPIED</th>
                        <th>NUMBER OF ROOMS</th>
                        <th>TENANT</th>
                        <th>ACTIONS</th>
                      </tr>
                    </thead>
                    <tfoot>
                      <tr>
                        <th>NAME</th>
                        <th>LANDLORD</th>
                        <th>DEPOSIT</th>
                        <th>TYPE</th>
                        <th>RENT</th>
                        <th>IS OCCUPIED</th>
                        <th>NUMBER OF ROOMS</th>
                        <th>TENANT</th>
                        <th>ACTIONS</th>
                      </tr>
                    </tfoot>
                    {loading ? (
                      <Loader />
                    ) : (
                      <tbody>
                        {propertys.map((property) => (
                          <tr key={property._id}>
                            <td>{property.givenName} </td>

                            <td>
                              {" "}
                              {property.landlord === null ? (
                                <Badge className="badge badge-danger">
                                  No Landlord
                                </Badge>
                              ) : (
                                property.landlord && property.landlord.fullName
                              )}{" "}
                            </td>
                            <td> {property.deposit} </td>
                            <td> {property.type} </td>
                            <td> {property.rent} </td>
                            <td>
                              {" "}
                              {property.isOccupied === false ? (
                                <Badge className="badge badge-danger">
                                  False{" "}
                                </Badge>
                              ) : (
                                <Badge className="badge badge-success">
                                  True{" "}
                                </Badge>
                              )}{" "}
                            </td>
                            <td> {property.numRooms} </td>
                            <td>
                              {" "}
                              {property.tenant === null ? (
                                <Badge className="badge badge-danger">
                                  No tenant
                                </Badge>
                              ) : (
                                property.tenant &&
                                property.tenant.firstName +
                                  " " +
                                  property.tenant.lastName
                              )}{" "}
                            </td>
                            <td>
                              {
                                <td>
                                  <LinkContainer
                                    to={`/listproperty/${property._id}`}
                                  >
                                    <Button className="btn btn-primary btn-sm mb-2">
                                      <i className="fas fa-edit"></i>
                                    </Button>
                                  </LinkContainer>
                                  <Button
                                    onClick={() => deleteHandler(property._id)}
                                    className="btn btn-danger btn-sm"
                                  >
                                    <i className="fas fa-trash"></i>
                                  </Button>
                                </td>
                              }
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    )}
                  </table>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ListPropertyScreen;
