import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import { toast } from "react-toastify";
import { LinkContainer } from "react-router-bootstrap";
import { tenantList, tenantDelete } from "../actions/tenantActions";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import { TENANT_UPDATE_RESET } from "../constants/tenantConstants";

const ListTenantsScreen = ({ history }) => {
  const dispatch = useDispatch();
  const listTenants = useSelector((state) => state.listTenants);
  const { error, loading, tenants } = listTenants;
  const deleteTenant = useSelector((state) => state.deleteTenant);
  const { success, loading: loadingDelete } = deleteTenant;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  useEffect(() => {
    if (userInfo) {
      dispatch(tenantList());
      dispatch({ type: TENANT_UPDATE_RESET });
    } else {
      history.push("/");
    }
  }, [history, dispatch, userInfo, loadingDelete, success]);

  const deleteHandler = (id) => {
    if (window.confirm("Are you sure you want to delete the tenant?")) {
      //dispatch
      dispatch(tenantDelete(id));
    }
  };
  return (
    <>
      {userInfo && (
        <>
          <Sidebar />
          <div class="container-fluid overflow-hidden">
            <Navbar />
            <h1 class="h3 mb-2 text-gray-800">VIEW TENANT</h1>
            {success && toast("Tenant deleted successfully")}

            <p class="mb-4">
              <Link to="/tenants">
                <Button className="btn btn-success">
                  {" "}
                  <i className="fa fa-plus"></i>Add ADD TENANT{" "}
                </Button>
              </Link>
            </p>

            <div class="card shadow mb-4">
              <div class="card-header py-3">
                <h6 class="m-0 font-weight-bold text-primary">
                  VIEW ALL LANDLORDS
                </h6>
              </div>
              <div class="card-body">
                <div class="table-responsive">
                  <table
                    class="table table-bordered"
                    id="dataTable"
                    width="100%"
                  >
                    <thead>
                      <tr>
                        <th>FIRST NAME</th>

                        <th>ID NUMBER</th>
                        <th>PHONE NUMBER</th>
                        <th>EMAIL</th>
                        <th>EMERGENCY CONTACTS NAME</th>
                        <th>EMERGENCY CONTACTS PHONE NUMBER</th>
                        <th>OCCUPATION</th>
                        <th>ACTIONS</th>
                      </tr>
                    </thead>
                    <tfoot>
                      <tr>
                        <th>FIRST NAME</th>

                        <th>ID NUMBER</th>
                        <th>PHONE NUMBER</th>
                        <th>EMAIL</th>
                        <th>EMERGENCY CONTACTS NAME</th>
                        <th>EMERGENCY CONTACTS PHONE NUMBER</th>
                        <th>OCCUPATION</th>
                        <th>ACTIONS</th>
                      </tr>
                    </tfoot>
                    <tbody>
                      {tenants.map((tenant) => (
                        <tr key={tenant._id}>
                          <td>{tenant.firstName} </td>
                          <td> {tenant.idNumber} </td>
                          <td> {tenant.phoneNumber} </td>
                          <td> {tenant.email} </td>
                          <td> {tenant.emergencyName} </td>
                          <td> {tenant.emergencyPhoneNumber} </td>
                          <td> {tenant.occupation} </td>
                          <td>
                            <LinkContainer to={`/listtenants/${tenant._id}`}>
                              <Button className="btn btn-primary btn-sm btn-circle mb-1">
                                <i className="fas fa-edit"></i>
                              </Button>
                            </LinkContainer>
                            <Button
                              onClick={() => deleteHandler(tenant._id)}
                              className="btn btn-danger btn-sm btn-circle"
                            >
                              <i className="fas fa-trash"></i>
                            </Button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ListTenantsScreen;
