import {
  TENANT_REGISTER_FAIL,
  TENANT_REGISTER_REQUEST,
  TENANT_REGISTER_RESET,
  TENANT_REGISTER_SUCCESS,
  TENANT_LIST_REQUEST,
  TENANT_LIST_FAIL,
  TENANT_LIST_SUCCESS,
  TENANT_DELETE_REQUEST,
  TENANT_DELETE_SUCCESS,
  TENANT_DELETE_FAIL,
  TENANT_DETAILS_REQUEST,
  TENANT_DETAILS_SUCCESS,
  TENANT_DETAILS_FAIL,
  TENANT_DETAILS_RESET,
  TENANT_UPDATE_SUCCESS,
  TENANT_UPDATE_REQUEST,
  TENANT_UPDATE_FAIL,
  TENANT_UPDATE_RESET,
} from "../constants/tenantConstants";

export const registerTenantReducer = (state = {}, action) => {
  switch (action.type) {
    case TENANT_REGISTER_REQUEST:
      return {
        loading: true,
      };
    case TENANT_REGISTER_SUCCESS:
      return { loading: false, success: true, tenant: action.payload };
    case TENANT_REGISTER_FAIL:
      return { loading: false, error: action.payload };
    case TENANT_REGISTER_RESET:
      return {};
    default:
      return state;
  }
};

export const listTenantsReducer = (state = { tenants: [] }, action) => {
  switch (action.type) {
    case TENANT_LIST_REQUEST:
      return {
        loading: true,
        tenants: [],
      };
    case TENANT_LIST_SUCCESS:
      return { loading: false, tenants: action.payload };
    case TENANT_LIST_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const deleteTenantReducer = (state = {}, action) => {
  switch (action.type) {
    case TENANT_DELETE_REQUEST:
      return {
        loading: true,
      };
    case TENANT_DELETE_SUCCESS:
      return { loading: false, success: true };
    case TENANT_DELETE_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const getTenantReducer = (state = { tenant: {} }, action) => {
  switch (action.type) {
    case TENANT_DETAILS_REQUEST:
      return {
        loading: true,
        ...state,
      };
    case TENANT_DETAILS_SUCCESS:
      return { loading: false, tenant: action.payload };
    case TENANT_DETAILS_FAIL:
      return { loading: false, error: action.payload };
    case TENANT_DETAILS_RESET:
      return {};
    default:
      return state;
  }
};
export const updateTenantReducer = (state = { tenant: {} }, action) => {
  switch (action.type) {
    case TENANT_UPDATE_REQUEST:
      return {
        loading: true,
      };
    case TENANT_UPDATE_SUCCESS:
      return { loading: false, success: true, payload: action.payload };
    case TENANT_UPDATE_FAIL:
      return { loading: false, error: action.payload };
    case TENANT_UPDATE_RESET:
      return {};
    default:
      return state;
  }
};
