import {
  PROPERTY_REGISTER_REQUEST,
  PROPERTY_REGISTER_SUCCESS,
  PROPERTY_REGISTER_FAIL,
  PROPERTY_REGISTER_RESET,
  PROPERTY_LIST_REQUEST,
  PROPERTY_LIST_SUCCESS,
  PROPERTY_LIST_FAIL,
  PROPERTY_DELETE_REQUEST,
  PROPERTY_DELETE_SUCCESS,
  PROPERTY_DELETE_FAIL,
  PROPERTY_DETAILS_REQUEST,
  PROPERTY_DETAILS_SUCCESS,
  PROPERTY_DETAILS_FAIL,
  PROPERTY_DETAILS_RESET,
  PROPERTY_UPDATE_REQUEST,
  PROPERTY_UPDATE_SUCCESS,
  PROPERTY_UPDATE_FAIL,
  PROPERTY_UPDATE_RESET,
} from "../constants/propertyConstants";
export const registerPropertyReducer = (state = {}, action) => {
  switch (action.type) {
    case PROPERTY_REGISTER_REQUEST:
      return {
        loading: true,
      };
    case PROPERTY_REGISTER_SUCCESS:
      return { loading: false, success: true, property: action.payload };
    case PROPERTY_REGISTER_FAIL:
      return { loading: false, error: action.payload };
    case PROPERTY_REGISTER_RESET:
      return {};
    default:
      return state;
  }
};

export const listPropertyReducer = (state = { propertys: [] }, action) => {
  switch (action.type) {
    case PROPERTY_LIST_REQUEST:
      return {
        loading: true,
        propertys: [],
      };
    case PROPERTY_LIST_SUCCESS:
      return { loading: false, propertys: action.payload };
    case PROPERTY_LIST_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const deletePropertyReducer = (state = {}, action) => {
  switch (action.type) {
    case PROPERTY_DELETE_REQUEST:
      return {
        loading: true,
      };
    case PROPERTY_DELETE_SUCCESS:
      return { loading: false, success: true };
    case PROPERTY_DELETE_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
    case "PROPERTY_DELETE_RESET":
      return {};
  }
};

export const getPropertyReducer = (state = { property: {} }, action) => {
  switch (action.type) {
    case PROPERTY_DETAILS_REQUEST:
      return {
        loading: true,
        ...state,
      };
    case PROPERTY_DETAILS_SUCCESS:
      return { loading: false, property: action.payload };
    case PROPERTY_DETAILS_FAIL:
      return { loading: false, error: action.payload };
    case PROPERTY_DETAILS_RESET:
      return {};
    default:
      return state;
  }
};
export const updatePropertyReducer = (state = { property: {} }, action) => {
  switch (action.type) {
    case PROPERTY_UPDATE_REQUEST:
      return {
        loading: true,
      };
    case PROPERTY_UPDATE_SUCCESS:
      return { loading: false, success: true, payload: action.payload };
    case PROPERTY_UPDATE_FAIL:
      return { loading: false, error: action.payload };
    case PROPERTY_UPDATE_RESET:
      return {};
    default:
      return state;
  }
};
