import {
  ROOM_DELETE_FAIL,
  ROOM_DELETE_REQUEST,
  ROOM_DELETE_SUCCESS,
  ROOM_DETAILS_FAIL,
  ROOM_DETAILS_REQUEST,
  ROOM_DETAILS_RESET,
  ROOM_DETAILS_SUCCESS,
  ROOM_GETTENANT_FAIL,
  ROOM_GETTENANT_REQUEST,
  ROOM_GETTENANT_SUCCESS,
  ROOM_GET_FAIL,
  ROOM_GET_REQUEST,
  ROOM_GET_SUCCESS,
  ROOM_LIST_FAIL,
  ROOM_LIST_REQUEST,
  ROOM_LIST_SUCCESS,
  ROOM_REGISTER_FAIL,
  ROOM_REGISTER_REQUEST,
  ROOM_REGISTER_RESET,
  ROOM_REGISTER_SUCCESS,
  ROOM_UPDATE_FAIL,
  ROOM_UPDATE_REQUEST,
  ROOM_UPDATE_RESET,
  ROOM_UPDATE_SUCCESS,
} from "../constants/roomContants";

export const registerRoomReducer = (state = {}, action) => {
  switch (action.type) {
    case ROOM_REGISTER_REQUEST:
      return {
        loading: true,
      };
    case ROOM_REGISTER_SUCCESS:
      return { loading: false, success: true, room: action.payload };
    case ROOM_REGISTER_FAIL:
      return { loading: false, error: action.payload };
    case ROOM_REGISTER_RESET:
      return {};
    default:
      return state;
  }
};

export const listRoomsReducer = (state = { rooms: [] }, action) => {
  switch (action.type) {
    case ROOM_LIST_REQUEST:
      return {
        loading: true,
        rooms: [],
      };
    case ROOM_LIST_SUCCESS:
      return { loading: false, rooms: action.payload };
    case ROOM_LIST_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const deleteRoomReducer = (state = {}, action) => {
  switch (action.type) {
    case ROOM_DELETE_REQUEST:
      return {
        loading: true,
      };
    case ROOM_DELETE_SUCCESS:
      return { loading: false, success: true };
    case ROOM_DELETE_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const getRoomReducer = (state = { room: {} }, action) => {
  switch (action.type) {
    case ROOM_DETAILS_REQUEST:
      return {
        loading: true,
        ...state,
      };
    case ROOM_DETAILS_SUCCESS:
      return { loading: false, room: action.payload };
    case ROOM_DETAILS_FAIL:
      return { loading: false, error: action.payload };
    case ROOM_DETAILS_RESET:
      return {};
    default:
      return state;
  }
};

export const getRoomsByApartmentReducer = (
  state = { allRooms: [] },
  action
) => {
  switch (action.type) {
    case ROOM_GET_REQUEST:
      return {
        loading: true,
        allRooms: [],
      };
    case ROOM_GET_SUCCESS:
      return { loading: false, allRooms: action.payload };
    case ROOM_GET_FAIL:
      return { loading: false, error: action.payload };
    case "ROOM_GET_RESET":
      return {};
    default:
      return state;
  }
};

export const getTenantByRoomReducer = (
  state = { allTenants: [] },
  action
) => {
  switch (action.type) {
    case ROOM_GETTENANT_REQUEST:
      return {
        loading: true,
        allTenants: [],
      };
    case ROOM_GETTENANT_SUCCESS:
      return { loading: false, allTenants: action.payload };
    case ROOM_GETTENANT_FAIL:
      return { loading: false, error: action.payload };
    case "ROOM_GET_RESET":
      return {};
    default:
      return state;
  }
};

export const updateRoomReducer = (state = { room: {} }, action) => {
  switch (action.type) {
    case ROOM_UPDATE_REQUEST:
      return {
        loading: true,
      };
    case ROOM_UPDATE_SUCCESS:
      return { loading: false, success: true, payload: action.payload };
    case ROOM_UPDATE_FAIL:
      return { loading: false, error: action.payload };
    case ROOM_UPDATE_RESET:
      return {};
    default:
      return state;
  }
};
