import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import validator from "validator";
import Loader from "../components/Loader";
import { toast } from "react-toastify";
import { useDispatch, useSelector } from "react-redux";
import Select from "react-select";
import { addTenant } from "../actions/tenantActions";
import { TENANT_REGISTER_RESET } from "../constants/tenantConstants";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
const AddTenant = ({ history }) => {
  var occupationList = [
    {
      value: "student",
      label: "student",
    },
    {
      value: "employed",
      label: "Employed",
    },
    {
      value: "unemployed",
      label: "Unemployed",
    },
  ];
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [idNumber, setIdNumber] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [emergencyName, setEmergencyName] = useState("");
  const [occupation, setOccupation] = useState(occupationList.value);
  const [roomAssigned, setRoomAssigned] = useState();
  const [emergencyPhoneNumber, setEmergencyPhoneNumber] = useState("");

  const validatePhoneNumber = (phoneNumber) => {
    const isValidPhoneNumber = validator.isMobilePhone(phoneNumber);
    return isValidPhoneNumber;
  };

  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const registerTenant = useSelector((state) => state.registerTenant);
  const { loading, error, success } = registerTenant;

  useEffect(() => {
    if (!userInfo) {
      history.push("/");
    }
    if (success) {
      history.push("/listtenants");
      dispatch({
        type: TENANT_REGISTER_RESET,
      });
    }
  }, [success, history, userInfo, dispatch]);
  const onSubmitHandler = (e) => {
    e.preventDefault();

    dispatch(
      addTenant(
        firstName,
        lastName,
        email,
        phoneNumber,
        idNumber,
        emergencyPhoneNumber,
        emergencyName,
        occupation,
        roomAssigned
      )
    );
  };
  return (
    <>
      {userInfo && (
        <>
          <Sidebar />

          <div className="container">
            <Navbar />
            <div className="card o-hidden border-0 shadow-lg my-5">
              <div className="card-body p-0">
                <div className="row">
                  <div className="col-lg-12 col-md-12">
                    <div className="p-5">
                      <div className="text-center">
                        {error && toast(error)}
                        {success && toast("Tenant registered successfully")}
                        <Link to="/listtenants">
                          {" "}
                          <Button className="btn btn-outline-info">
                            VIEW ALL TENANTS
                          </Button>{" "}
                        </Link>
                        <br />
                        <h1 className="h4 text-gray-900 mb-4">Add TENANT</h1>
                      </div>
                      <form className="user" onSubmit={onSubmitHandler}>
                        <div className="row">
                          <div className="col">
                            <div class="form-group">
                              <input
                                type="text"
                                value={firstName}
                                onChange={(e) => setFirstName(e.target.value)}
                                class="form-control form-control-user"
                                id="exampleInputName"
                                required
                                aria-describedby="name"
                                placeholder="Enter First Name..."
                              />
                            </div>
                          </div>
                          <div className="col">
                            <div class="form-group">
                              <input
                                type="text"
                                value={lastName}
                                onChange={(e) => setLastName(e.target.value)}
                                class="form-control form-control-user"
                                id="exampleInputName"
                                required
                                aria-describedby="name"
                                placeholder="Enter Last Name..."
                              />
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col">
                            {" "}
                            <div class="form-group">
                              <input
                                type="email"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                class="form-control form-control-user"
                                id="exampleInputEmail"
                                required
                                aria-describedby="emailHelp"
                                placeholder="Enter Email Address..."
                              />
                            </div>
                          </div>
                          <div className="col">
                            <div class="form-group">
                              <input
                                type="number"
                                value={idNumber}
                                onChange={(e) => setIdNumber(e.target.value)}
                                class="form-control form-control-user"
                                id="number"
                                required
                                minLength="8"
                                aria-describedby="emailHelp"
                                placeholder="Enter Id Number..."
                              />
                            </div>
                          </div>
                          <div className="col">
                            {" "}
                            <div class="form-group">
                              <input
                                type="number"
                                value={phoneNumber}
                                onChange={(e) => setPhoneNumber(e.target.value)}
                                class="form-control form-control-user"
                                id="password"
                                required
                                minLength="10"
                                aria-describedby="emailHelp"
                                placeholder="Enter Phone Number..."
                              />
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col">
                            {" "}
                            <div class="form-group">
                              <input
                                type="text"
                                value={emergencyName}
                                onChange={(e) =>
                                  setEmergencyName(e.target.value)
                                }
                                class="form-control form-control-user"
                                id="emn"
                                required
                                aria-describedby="emailHelp"
                                placeholder="Enter Emergency Contact's name..."
                              />
                            </div>
                          </div>
                          <div className="col">
                            {" "}
                            <div class="form-group">
                              <input
                                type="number"
                                value={emergencyPhoneNumber}
                                onChange={(e) =>
                                  setEmergencyPhoneNumber(e.target.value)
                                }
                                class="form-control form-control-user"
                                id="emn"
                                required
                                minLength="10"
                                aria-describedby="emailHelp"
                                placeholder="Enter Emergency Contact's phone Number..."
                              />
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="offset-3 col-md-6 offset-3">
                            <div class="form-group">
                              <label htmlFor="Occupation">
                                Select Occupation
                              </label>
                              <Select
                                options={occupationList}
                                onChange={(e) => setOccupation(e.value)}
                              />
                            </div>
                          </div>
                        </div>

                        <div class="form-group">
                          {loading ? (
                            <Loader />
                          ) : (
                            <button
                              type="submit"
                              class="btn btn-primary btn-user ml-auto"
                            >
                              Add Tenant
                            </button>
                          )}
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default AddTenant;
