import { createStore, combineReducers, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";
import {
  userDetailsReducer,
  userLoginReducer,
  userRegisterReducer,
  userUpdateProfileReducer,
} from "./reducers/userReducers";
import {
  listLandlordReducer,
  registerLandlordReducer,
  deleteLandlordReducer,
  updateLandlordReducer,
  getLandlordReducer,
} from "./reducers/landlordReducers";
import {
  registerTenantReducer,
  listTenantsReducer,
  deleteTenantReducer,
  getTenantReducer,
  updateTenantReducer,
} from "./reducers/tenantReducers";
import {
  deletePropertyReducer,
  getPropertyReducer,
  listPropertyReducer,
  registerPropertyReducer,
  updatePropertyReducer,
} from "./reducers/propertyReducers";
import {
  deleteRoomReducer,
  getRoomReducer,
  getRoomsByApartmentReducer,
  getTenantByRoomReducer,
  listRoomsReducer,
  registerRoomReducer,
  updateRoomReducer,
} from "./reducers/roomReducers";

const reducer = combineReducers({
  userLogin: userLoginReducer,
  userRegister: userRegisterReducer,
  userDetails: userDetailsReducer,
  userUpdateProfile: userUpdateProfileReducer,
  registerLandlord: registerLandlordReducer,
  listLandlord: listLandlordReducer,
  deleteLandlord: deleteLandlordReducer,
  updateLandlords: updateLandlordReducer,
  getLandlord: getLandlordReducer,
  registerTenant: registerTenantReducer,
  listTenants: listTenantsReducer,
  deleteTenant: deleteTenantReducer,
  getTenant: getTenantReducer,
  updateTenant: updateTenantReducer,
  registerProperty: registerPropertyReducer,
  listProperty: listPropertyReducer,
  deleteProperty: deletePropertyReducer,
  getProperty: getPropertyReducer,
  updateProperty: updatePropertyReducer,
  registerRoom: registerRoomReducer,
  listRooms: listRoomsReducer,
  deleteRoom: deleteRoomReducer,
  getRoom: getRoomReducer,
  updateRoom: updateRoomReducer,
  getRoomsByApartment: getRoomsByApartmentReducer,
  getTenantByRoom: getTenantByRoomReducer,
});

const userInfoFromStorage = localStorage.getItem("userInfo")
  ? JSON.parse(localStorage.getItem("userInfo"))
  : null;
const initialState = {
  userLogin: { userInfo: userInfoFromStorage },
};

const middleware = [thunk];

const store = createStore(
  reducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
