import {
  LANDLORD_REGISTER_REQUEST,
  LANDLORD_REGISTER_SUCCESS,
  LANDLORD_REGISTER_FAIL,
  LANDLORD_DETAILS_REQUEST,
  LANDLORD_DETAILS_SUCCESS,
  LANDLORD_DETAILS_FAIL,
  LANDLORD_DELETE_FAIL,
  LANDLORD_DELETE_REQUEST,
  LANDLORD_DELETE_SUCCESS,
  LANDLORD_UPDATE_REQUEST,
  LANDLORD_UPDATE_RESET,
  LANDLORD_UPDATE_FAIL,
  LANDLORD_UPDATEDETAILS_REQUEST,
  LANDLORD_UPDATEDETAILS_SUCCESS,
  LANDLORD_UPDATEDETAILS_FAIL,
  LANDLORD_UPDATE_SUCCESS,
} from "../constants/landlordConstants";
import axios from "axios";

export const addLandlord =
  (fullName, email, idNumber, phoneNumber) => async (dispatch, getState) => {
    try {
      dispatch({
        type: LANDLORD_REGISTER_REQUEST,
      });
      const {
        userLogin: { userInfo },
      } = getState();
      const config = {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      const { data } = await axios.post(
        "/api/landlords",
        { fullName, email, idNumber, phoneNumber },
        config
      );
      dispatch({
        type: LANDLORD_REGISTER_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: LANDLORD_REGISTER_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      });
    }
  };

export const listLandlords = () => async (dispatch, getState) => {
  try {
    dispatch({
      type: LANDLORD_DETAILS_REQUEST,
    });
    const {
      userLogin: { userInfo },
    } = getState();
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await axios.get("/api/landlords", config);
    dispatch({
      type: LANDLORD_DETAILS_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: LANDLORD_DETAILS_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    });
  }
};

