import axios from "axios";
import {
  PROPERTY_DELETE_FAIL,
  PROPERTY_DELETE_REQUEST,
  PROPERTY_DELETE_SUCCESS,
  PROPERTY_DETAILS_FAIL,
  PROPERTY_DETAILS_REQUEST,
  PROPERTY_DETAILS_SUCCESS,
  PROPERTY_LIST_FAIL,
  PROPERTY_LIST_REQUEST,
  PROPERTY_LIST_SUCCESS,
  PROPERTY_REGISTER_FAIL,
  PROPERTY_REGISTER_REQUEST,
  PROPERTY_REGISTER_SUCCESS,
  PROPERTY_UPDATE_FAIL,
  PROPERTY_UPDATE_REQUEST,
  PROPERTY_UPDATE_SUCCESS,
} from "../constants/propertyConstants";
export const addProperty =
  (
    givenName,
    landlord,
    deposit,
    type,
    rent,
    city,
    county,
    isOccupied,
    tenant,
    numRooms
  ) =>
  async (dispatch, getState) => {
    try {
      dispatch({
        type: PROPERTY_REGISTER_REQUEST,
      });
      const {
        userLogin: { userInfo },
      } = getState();
      const config = {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      const { data } = await axios.post(
        "/api/property",
        {
          givenName,
          landlord,
          deposit,
          type,
          rent,
          city,
          county,
          isOccupied,
          tenant,
          numRooms,
        },
        config
      );
      dispatch({
        type: PROPERTY_REGISTER_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: PROPERTY_REGISTER_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      });
    }
  };

export const propertyList = () => async (dispatch, getState) => {
  try {
    dispatch({
      type: PROPERTY_LIST_REQUEST,
    });
    const {
      userLogin: { userInfo },
    } = getState();
    const config = {
      headers: {
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await axios.get("/api/property", config);
    dispatch({
      type: PROPERTY_LIST_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: PROPERTY_LIST_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    });
  }
};

