import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";

const Sidebar = () => {
  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  return (
    <ul
      className="navbar-nav bg-gradient-primary sidebar sidebar-dark accordion"
      id="accordionSidebar"
      style={{ scrollMargin: true }}
    >
      <Link
        className="sidebar-brand d-flex align-items-center justify-content-center"
        to="index.html"
      >
        <div className="sidebar-brand-icon rotate-n-15"></div>
        <div className="sidebar-brand-text mx-3">MANYATTA RENTAL SYSTEM</div>
      </Link>

      <hr className="sidebar-divider my-0" />

      <li className="nav-item active">
        <Link className="nav-link" to="/dashboard">
          <i className="fas fa-fw fa-tachometer-alt"></i>
          <span>Dashboard</span>
        </Link>
      </li>

      <hr className="sidebar-divider" />

      <div className="sidebar-heading">Routes</div>

      <li className="nav-item">
        <Link
          className="nav-link collapsed"
          to="#"
          data-toggle="collapse"
          data-target="#collapseTwo"
          aria-expanded="true"
          aria-controls="collapseTwo"
        >
          <i className="fas fa-user"></i>
          <span>Landlords</span>
        </Link>
        <div
          id="collapseTwo"
          className="collapse"
          aria-labelledby="headingTwo"
          data-parent="#accordionSidebar"
        >
          {userInfo && userInfo?.isAdmin && (
            <div className="bg-white py-2 collapse-inner rounded">
              <Link className="collapse-item" to="/landlords">
                Add Landlord
              </Link>
              <Link className="collapse-item" to="/listlandlords">
                View Landlords
              </Link>
            </div>
          )}
        </div>
      </li>

      <hr className="sidebar-divider" />

      <li className="nav-item">
        <Link
          className="nav-link collapsed"
          to="#"
          data-toggle="collapse"
          data-target="#collapseThree"
          aria-expanded="true"
          aria-controls="collapseThree"
        >
          <i className="fas fa-user-circle"></i>
          <span>Tenants</span>
        </Link>
        <div
          id="collapseThree"
          className="collapse"
          aria-labelledby="headingTwo"
          data-parent="#accordionSidebar"
        >
          <div className="bg-white py-2 collapse-inner rounded">
            <Link className="collapse-item" to="/tenants">
              Add Tenant
            </Link>
            <Link className="collapse-item" to="/listtenants">
              View Tenants
            </Link>
          </div>
        </div>
      </li>

      <hr className="sidebar-divider" />

      <li className="nav-item">
        <Link
          className="nav-link collapsed"
          to="#"
          data-toggle="collapse"
          data-target="#collapse3"
          aria-expanded="true"
          aria-controls="collapseThree"
        >
          <i className="fas fa-building"></i>
          <span>Property</span>
        </Link>
        <div
          id="collapse3"
          className="collapse"
          aria-labelledby="headingTwo"
          data-parent="#accordionSidebar"
        >
          <div className="bg-white py-2 collapse-inner rounded">
            <Link className="collapse-item" to="/property">
              Add Property
            </Link>
            <Link className="collapse-item" to="/listproperty">
              View Property
            </Link>
            <Link className="collapse-item" to="/rooms">
              <i className="fas  fa-home"></i> Add Rooms
            </Link>
            <Link className="collapse-item" to="/listrooms">
              View Rooms
            </Link>
          </div>
        </div>
      </li>

      <hr className="sidebar-divider" />

      <li className="nav-item">
        <Link
          className="nav-link collapsed"
          to="#"
          data-toggle="collapse"
          data-target="#collapse5"
          aria-expanded="true"
          aria-controls="collapse5"
        >
          <i className="fas fa-sticky-note"></i>
          <span>Invoice</span>
        </Link>
        <div
          id="collapse5"
          className="collapse"
          aria-labelledby="headingTwo"
          data-parent="#accordionSidebar"
        >
          <div className="bg-white py-2 collapse-inner rounded">
            <Link className="collapse-item" to="/invoice">
              Invoices
            </Link>
            <Link className="collapse-item" to="/invoice">
              send Invoice
            </Link>
          </div>
        </div>
      </li>

      <hr className="sidebar-divider" />

      <hr className="sidebar-divider d-none d-md-block" />

      <div className="text-center d-none d-md-inline">
        <button className="rounded-circle border-0" id="sidebarToggle"></button>
      </div>
    </ul>
  );
};

export default Sidebar;
