import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import Loader from "../components/Loader";
import { LinkContainer } from "react-router-bootstrap";
import { listLandlords, landlordDelete } from "../actions/landlordActions";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import { Button } from "react-bootstrap";
import { LANDLORD_DELETE_RESET } from "../constants/landlordConstants";

const ListLandlordsScreen = ({ history }) => {
  const dispatch = useDispatch();
  const listLandlord = useSelector((state) => state.listLandlord);
  const { error, loading, landlords } = listLandlord;
  const deleteLandlord = useSelector((state) => state.deleteLandlord);
  const { success, loading: loadingDelete } = deleteLandlord;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  useEffect(() => {
    if (userInfo) {
      dispatch(listLandlords());
    } else {
      history.push("/");
    }
  }, [history, dispatch, userInfo, success]);

  const deleteHandler = (id) => {
    if (window.confirm("Are you sure you want to delete the landlord?")) {
      //dispatch
      dispatch(landlordDelete(id));
    }
  };
  return (
    <>
      {userInfo && (
        <>
          <Sidebar />
          <div class="container-fluid">
            <Navbar />
            <h1 class="h3 mb-2 text-gray-800">LANDLORDS</h1>
            {success && toast("Lanlord deleted successfully")}

            <p class="mb-4">
              <Link to="/landlords">
                <Button className="btn btn-success">
                  {" "}
                  <i className="fa fa-plus"></i>Add Landlord{" "}
                </Button>
              </Link>
            </p>

            <div class="card shadow mb-4">
              <div class="card-header py-3">
                <h6 class="m-0 font-weight-bold text-primary">
                  VIEW ALL LANDLORDS
                </h6>
              </div>
              <div class="card-body">
                <div class="table-responsive">
                  <table
                    class="table table-bordered"
                    id="dataTable"
                    width="100%"
                    cellspacing="0"
                  >
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th>FULLNAME</th>
                        <th>ID NUMBER</th>
                        <th>PHONE NUMBER</th>
                        <th>EMAIL</th>
                        <th>ACTIONS</th>
                      </tr>
                    </thead>
                    <tfoot>
                      <tr>
                        <th>ID</th>
                        <th>FULLNAME</th>
                        <th>ID NUMBER</th>
                        <th>PHONE NUMBER</th>
                        <th>EMAIL</th>
                        <th>ACTIONS</th>
                      </tr>
                    </tfoot>
                    {loading ? (
                      <Loader />
                    ) : (
                      <tbody>
                        {landlords.map((landlord) => (
                          <tr key={landlord._id}>
                            <td>{landlord._id} </td>
                            <td>{landlord.fullName} </td>
                            <td> {landlord.idNumber} </td>
                            <td> {landlord.phoneNumber} </td>
                            <td> {landlord.email} </td>
                            <td>
                              {
                                <td>
                                  <LinkContainer
                                    to={`/listlandlords/${landlord._id}`}
                                  >
                                    <Button className="btn btn-primary btn-sm">
                                      <i className="fas fa-edit"></i>
                                    </Button>
                                  </LinkContainer>
                                  <Button
                                    onClick={() => deleteHandler(landlord._id)}
                                    className="btn btn-danger btn-sm ml-3"
                                  >
                                    <i className="fas fa-trash"></i>
                                  </Button>
                                </td>
                              }
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    )}
                  </table>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export default ListLandlordsScreen;
