import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import Sidebar from "../components/Sidebar";
import validator from "validator";
import Loader from "../components/Loader";
import { toast } from "react-toastify";
import { useDispatch, useSelector } from "react-redux";
import Select from "react-select";
import { tenantUpdate, getTenants } from "../actions/tenantActions";
import { Link } from "react-router-dom";
import { TENANT_UPDATE_RESET } from "../constants/tenantConstants";

const EditTenantsScreen = ({ history, match }) => {
  const Id = match.params.id;
  var occupationList = [
    {
      value: "student",
      label: "student",
    },
    {
      value: "employed",
      label: "Employed",
    },
    {
      value: "unemployed",
      label: "Unemployed",
    },
  ];
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [idNumber, setIdNumber] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [emergencyName, setEmergencyName] = useState("");
  const [occupation, setOccupation] = useState(occupationList.value);
  const [roomAssigned, setRoomAssigned] = useState();
  const [emergencyPhoneNumber, setEmergencyPhoneNumber] = useState("");

  const validatePhoneNumber = (phoneNumber) => {
    const isValidPhoneNumber = validator.isMobilePhone(phoneNumber);
    return isValidPhoneNumber;
  };

  const dispatch = useDispatch();

  const updateTenant = useSelector((state) => state.updateTenant);
  const { loading, success, error } = updateTenant;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  const getTenant = useSelector((state) => state.getTenant);
  const { tenant } = getTenant;

  useEffect(() => {
    if (!userInfo) {
      history.push("/");
    }
    if (tenant.name || tenant._id !== Id) {
      dispatch(getTenants(Id));
    } else {
      setFirstName(tenant.firstName);
      setLastName(tenant.lastName);
      setEmail(tenant.email);
      setPhoneNumber(tenant.phoneNumber);
      setIdNumber(tenant.idNumber);
      setEmergencyPhoneNumber(tenant.emergencyPhoneNumber);
      setEmergencyName(tenant.emergencyName);
      setOccupation(tenant.occupation);
      setRoomAssigned(tenant.roomAssigned);
    }
    if (success) {
      history.push("/listtenants");
      dispatch({ type: TENANT_UPDATE_RESET });
    }
  }, [dispatch, history, Id, tenant, success, userInfo]);
  const onSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(
      tenantUpdate({
        _id: Id,
        firstName,
        lastName,
        email,
        phoneNumber,
        idNumber,
        emergencyPhoneNumber,
        emergencyName,
        occupation,
        roomAssigned,
      })
    );
  };
  return (
    <>
      <Sidebar />

      <div className="container">
        <Navbar />
        <div className="card o-hidden border-0 shadow-lg my-5">
          <div className="card-body p-0">
            <div className="row">
              <div className="col-lg-12 col-md-12">
                <div className="p-5">
                  <div className="text-center">
                    <Link to="/listtenants" className="btn btn-outline-info">
                      Go Back
                    </Link>
                    {error && toast({ error })}
                    <br />
                    {success && toast("Tenant Updated successfully")}
                    <h1 className="h4 text-gray-900 mb-4">Edit TENANT</h1>
                  </div>
                  <form className="user" onSubmit={onSubmitHandler}>
                    <div className="row">
                      <div className="col">
                        <div class="form-group">
                          <input
                            type="text"
                            value={firstName}
                            onChange={(e) => setFirstName(e.target.value)}
                            class="form-control form-control-user"
                            id="exampleInputName"
                            required
                            minLength="6"
                            aria-describedby="name"
                            placeholder="Enter First Name..."
                          />
                        </div>
                      </div>
                      <div className="col">
                        <div class="form-group">
                          <input
                            type="text"
                            value={lastName}
                            onChange={(e) => setLastName(e.target.value)}
                            class="form-control form-control-user"
                            id="exampleInputName"
                            required
                            minLength="6"
                            aria-describedby="name"
                            placeholder="Enter Last Name..."
                          />
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <input
                            type="email"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            class="form-control form-control-user"
                            id="exampleInputEmail"
                            required
                            aria-describedby="emailHelp"
                            placeholder="Enter Email Address..."
                          />
                        </div>
                      </div>
                      <div className="col">
                        <div class="form-group">
                          <input
                            type="number"
                            value={idNumber}
                            onChange={(e) => setIdNumber(e.target.value)}
                            class="form-control form-control-user"
                            id="number"
                            required
                            minLength="8"
                            aria-describedby="emailHelp"
                            placeholder="Enter Id Number..."
                          />
                        </div>
                      </div>
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <input
                            type="number"
                            value={phoneNumber}
                            onChange={(e) => setPhoneNumber(e.target.value)}
                            class="form-control form-control-user"
                            id="password"
                            required
                            minLength="10"
                            aria-describedby="emailHelp"
                            placeholder="Enter Phone Number..."
                          />
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <input
                            type="text"
                            value={emergencyName}
                            onChange={(e) => setEmergencyName(e.target.value)}
                            class="form-control form-control-user"
                            id="emn"
                            required
                            minLength="10"
                            aria-describedby="emailHelp"
                            placeholder="Enter Emergency Contact's name..."
                          />
                        </div>
                      </div>
                      <div className="col">
                        {" "}
                        <div class="form-group">
                          <input
                            type="number"
                            value={emergencyPhoneNumber}
                            onChange={(e) =>
                              setEmergencyPhoneNumber(e.target.value)
                            }
                            class="form-control form-control-user"
                            id="emn"
                            required
                            minLength="10"
                            aria-describedby="emailHelp"
                            placeholder="Enter Emergency Contact's phone Number..."
                          />
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="offset-3 col-md-6 offset-3">
                        <div class="form-group">
                          <label htmlFor="Occupation">Select Occupation</label>
                          <Select
                            options={occupationList}
                            onChange={(e) => setOccupation(e.value)}
                          />
                        </div>
                      </div>
                    </div>

                    <div class="form-group">
                      {loading ? (
                        <Loader />
                      ) : (
                        <button
                          type="submit"
                          class="btn btn-primary btn-user ml-auto"
                        >
                          Update Tenant
                        </button>
                      )}
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default EditTenantsScreen;
